@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', sans-serif;
    background-color: #141414;
    background-color: #fff;
    color: #fff;
    text-align: center;
    cursor: none;
    overflow-x: hidden;
}

h1 {
    color: #fff;
    font-size: 3rem;
}

button {
    background-color: #007bff;
    font-size: 1.25rem;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    margin-top: 10px;
    border-radius: 5px;
    transition: .2s;
    &:hover {
        background-color: #0056b3;
    }
}

.container {
    display: flex;
    gap: 20px;
}

.image-wrapper {
    position: relative;
    img {
        display: block;
        display: noscale8,
    }
}

.cursor {
    position: fixed;
    pointer-events: none;
    z-index: 1000;
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.cursor__dot {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #e92b2b;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
    transition: width 0.3s ease, height 0.3s ease;
}

.cursor__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0, 0);
    text-align: center;
    font-size: 16px;
    text-transform: uppercase;
    color: #fff;
    opacity: 0;
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.cursor.large .cursor__dot {
    width: 72.5px;
    height: 72.5px;
}

.cursor.large .cursor__text {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1, 1);
}

.buttons-wrapper {
    width: 100%;
    margin: 10vh auto;
    padding: 17.5vh 20%;
    background-color: rgb(225, 225, 225);
    display: flex;
    justify-content: space-between;
    align-items: center;
    .button {
        font-family: 'Inter', sans-serif;
        color: #000;
        background-color: #fff;
        text-transform: uppercase;
        border-radius: 100px;
        position: relative;
        overflow: hidden;
        cursor: pointer;
        padding: 20px 35px;
    }
    .button-1 {
        display: flex;
        align-items: center;
        gap: 15px;
        .rounded {
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: #000;
            transition: all .3s ease-out;
        }
        .primary {
            margin: 0;
            font-size: 16px;
            transition: all .3s ease-out;
            z-index: 2;
        }
        .secondary {
            position: absolute;
            left: 100%;
            transition: transform .3s ease-out;
            z-index: 2;
            color: #fff;
            /*top: 22.5px;*/
        }
        &:hover {
            .rounded {
                transform: scale(40);
                background-color: #0a00c1;
            }
            .primary {
                color: #fff;
                transform: translateX(-25px);
            }
            .secondary {
                transform: translateX(-50px);
            }
        }
    }
    .button-2 {
        .primary {
            top: 0;
            position: relative;
            transition: top .4s cubic-bezier(.33, 1, .68, 1);
        }
        .secondary {
            position: absolute;
            top: 110%;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: top .4s cubic-bezier(.33, 1, .68, 1);
            p {
                position: absolute;
            }
            div {
                background-color: #c9ff18;
                width: 60%;
                height: 100%;
                border-radius: 50%;
                transition: all .4s cubic-bezier(.33, 1, .68, 1);
            }
        }
        &:hover {
            .primary {
                top: -40px;
            }
            .secondary {
                top: 0;
                div {
                    width: 100%;
                    border-radius: 100px;
                }
            }
        }
    }
    .button-3 {
        padding: 20px 0;
        .mask {
            overflow: hidden;
            position: relative;
            .slider {
                display: flex;
                position: relative;
                animation: text_slide 3.75s linear 0s infinite;
                transition: top .5s ease-out;
                top: 0;
                p {
                    margin: 0;
                    text-transform: uppercase;
                    white-space: nowrap;
                    &:nth-of-type(2) {
                        position: absolute;
                        left: calc(100% + 15px);
                    }
                }
            }
            .hover-text {
                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
                span {
                    position: relative;
                    color: transparent;
                    top: 18px;
                    transition: top .25s ease-out;
                }
            }
        }
        &:hover {
            background-color: #000;
            .slider {
                transition: top .75s ease-out;
                top: -18px;
            }
            .hover-text span {
                color: #fff;
                top: 0;
            }
        }
    }
}

@keyframes text_slide {
    100% {
        transform: translateX(calc(-100% - 15px));
    }
}

.intro-container {
    height: 125vh;
    overflow: hidden;
    img {
        object-fit: cover;
        height: 100%;
        width: 100%;
    }
}

.description-wrapper {
    background-color: #141414 !important;
    padding-top: 10rem;
    padding-bottom: 10rem;
    display: flex;
    justify-content: center;
    .description-heading {
        max-width: 57.5vw;
        font-size: 7.5vw;
        font-weight: 500;
        line-height: 1;
        text-align: center;
        text-wrap: balance;
        //user-select: none;
    }
}

.parallax-section {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    .parallax-section_text {
        position: relative;
        z-index: 10;
        padding: 3rem 1rem;
        color: #fff;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        font-weight: 500;
        mix-blend-mode: soft-light;
        p {
            width: 60vw;
            font-size: 2.5vw;
            user-select: none;
        }
        p:nth-of-type(2) {
            width: auto;
            font-size: 5.25vw;
            color: black;
        }
    }
    .img-container {
        position: fixed;
        width: 100%;
        top: -10vh;
        left: 0;
        height: 120vh;
        .img-wrapper {
            position: relative;
            width: 100%;
            img {
                object-fit: cover;
                height: 100%;
                width: 100%;
                filter: grayscale(100%) brightness(75%);
            }
        }
    }
}

.zoom-parallax-container {
    margin: 15vh 0 90vh;
    height: 300vh;
    position: relative;
    background-color: #141414;
    .sticky-div {
        position: sticky;
        top: 0px;
        height: 100vh;
        overflow: hidden;
        .wrapper {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            .img-container {
                width: 25vw;
                height: 25vh;
                position: relative;
                .name-wrapper {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    mix-blend-mode: difference;
                    .brand-name {
                        font-size: 3rem;
                        font-weight: 600;
                        color: #fff;
                        transform-origin: center;
                    }
                }
                img {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                }
            }
            &:nth-of-type(2) {
                .img-container {
                    top: -32.5vh;
                    left: 3.75vw;
                    width: 32.5vw;
                    height: 30vh;
                }
            }
            &:nth-of-type(3) {
                .img-container {
                    top: -15vh;
                    left: -25vw;
                    width: 20vw;
                    height: 55vh;
                }
            }
            &:nth-of-type(4) {
                .img-container {
                    left: 27.5vw;
                    width: 25vw;
                    height: 25vh;
                }
            }
            &:nth-of-type(5) {
                .img-container {
                    top: 30vh;
                    left: 5vw;
                    width: 20vw;
                    height: 27.5vh;
                }
            }
            &:nth-of-type(6) {
                .img-container {
                    top: 30vh;
                    left: -22.5vw;
                    width: 30vw;
                    height: 27.5vh;
                }
            }
            &:nth-of-type(7) {
                .img-container {
                    top: 27.5vh;
                    left: 27.5vw;
                    width: 17.5vw;
                    height: 22.5vh;
                }
            }
            &:nth-of-type(8) {
                .img-container {
                    top: -32.5vh;
                    left: 30vw;
                    width: 15vw;
                    height: 30vh;
                }
            }
        }
    }
}

.cards-parallax {
    margin-top: 50vh;
    margin-bottom: 50vh;
    .card-container {
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        position: sticky;
        top: 0px;
        .card {
            display: flex;
            flex-direction: column;
            position: relative;
            height: 500px;
            width: 1000px;
            height: 55vh;
            width: 60vw;
            border-radius: 25px;
            padding: 40px 50px 50px;
            transform-origin: top;
            h2 {
                font-family: 'Title';
                text-align: center;
                margin: 0px;
                font-size: 28px;
            }
            .card-Body {
                display: flex;
                height: 100%;
                margin-top: 50px;
                gap: 50px;
                overflow: hidden;
                .description {
                    width: 40%;
                    position: relative;
                    top: 10%;
                    text-align: left;
                    text-wrap: balance;
                    p {
                        font-size: 16px;
                        &::first-letter {
                            font-size: 28px;
                            font-family: 'Title';
                        }
                    }
                    span {
                        display: flex;
                        align-items: center;
                        gap: 5px;
                        a {
                            font-size: 12px;
                            text-decoration: underline;
                            cursor: pointer;
                        }
                    }
                }
                .image-Container {
                    position: relative;
                    width: 60%;
                    height: 100%;
                    border-radius: 20px;
                    overflow: hidden;
                    .inner {
                        width: 100%;
                        height: 100%;
                    }
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1100px) {
    body {
        cursor: auto;
    }
    .image-wrapper {
        img {
            display: none;
        }
    }
    .cursor {
        display: none
    }
}